@import "_variables";
@import "_mixins";

.portfolio {
  &__background {
    margin-top: 7vh;
    margin-left: 10vw;
    margin-right: 5vw;
    position: relative;
    width: 500px;
  }
  &__background:after {
    content: "";
    background: $backgroundColor;
    position: absolute;
    width: 100%;
    height: 70%;
    top: -40px;
    left: 40px;
    z-index: -1;
  }
  &__link {
    display: flex;
    justify-content: center;
    width: 100%;
    &--visible {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 5vw;
      width: 200px;
      height: 50px;
      background-color: $btnColor;
      border: none;
      text-decoration: none;
      color: black;
    }
    &--visible:active {
      transform: scale(0.99);
      outline: 1px solid #fff;
      outline-offset: -4px;
    }
    &--visible:hover {
      cursor: pointer;
    }
  }
  &__description {
    display: flex;
    flex-direction: column;
    padding: 2%;
    &--bold {
      font-weight: 700;
      font-size: 1.3rem;
    }
  }
  &__title {

    @include section__title;
  }
  &__galery {
    display: grid;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: 1fr;
      margin-top: 10vh;
  }
  &__picture {
    grid-area: 1 / 2 / 2 / 3;
    display: flex;
    max-width: 600px;
    height: auto;
    box-shadow: 4px -4px 4px $mainColor;
  }
  &__parallax {
    position: relative;
    height: 100px;
    width: 100%;
    background: transparent;
    &:after {
      content: "Portfolio";
      position: absolute;
      color: white;
      margin-top: 20px;
      margin-left: -5%;
    }
  }
}


